
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 10px;
}
.slide {
  position: absolute!important;
  left: 0;
  right: 0;
  transition: all 0.4s;
  /*overflow-x: hidden;*/
}
.slide-enter,
.slide-leave-active {
  opacity: 0;
  transform: translateX(-90px);
}



